let isRefreshing = false
let requests = [] //
module.exports = (vm) => {// vmvuex
	// 
	uni.$u.http.setConfig((config) => {
		/* config */
		// config.baseURL = "http://webapibeta.mzsale.com/mzato"; /*  */
		config.baseURL = "http://self.mzsale.com"; /*  */
		config.timeout = 60000;
		return config
	})

	// 
	uni.$u.http.interceptors.request.use((config) => { // async await 
		// dataundefined{}
		config.data = config.data || {}
		return config
	}, config => { // async await 
		return Promise.reject(config)
	})

	// 
	uni.$u.http.interceptors.response.use(async (response) => {
		if (response.data.error_code == 40002||response.data.error_code == 40004||response.data.error_code == 40005) {
			console.log("token 111", response)
			if(!isRefreshing){
				refreshFunc()
			}
			return new Promise((resolve) => {
				requests.push(token => {
					response.config.data.access_token=token
					resolve(uni.$u.http.request(response.config));
				})
			})
		}
		const data = response.data
		return data === undefined ? {} : data
	}, (response) => {
		return Promise.reject(response)
	})

	function refreshFunc() {//  token
		uni.setStorageSync("fdbh","0099")
		isRefreshing = true
		uni.request({
			url: "http://self.mzsale.com/mzsale/web/token",
			data: {
				"userid": uni.getStorageSync("userid"),
				"refresh_token": uni.getStorageSync("refresh_token")
			},
			method: "POST",
			header: {
				"Content-Type": "application/json"
			},
			success: (resh) => {
				console.log("app Token!!!!!!!!!", resh)
				if (resh.data.error_code == 0) {
					uni.setStorageSync("access_token", resh.data.access_token);
					isRefreshing = true
					let token = resh.data.access_token
					// token
					requests.forEach(item => item(token))
					requests = []
				} else if (resh.data.error_code == 40002) {
					loadRefresh()
				} else if (resh.data.error_code == 40004) { //refresh_token
					// isRefreshing = true
					// uni.$u.toast("refresh_token")
					// setTimeout(()=>{
					// 	uni.redirectTo({
					// 		url: "/pages/login/login?ifCheckAll=false"
					// 	});
					// },1800)
					loadRefresh()
				} else if (resh.data.error_code == 40005) { //refresh_token
					// isRefreshing = true
					// uni.$u.toast("refresh_token")
					// setTimeout(()=>{
					// 	uni.redirectTo({
					// 		url: "/pages/login/login?ifCheckAll=false"
					// 	});
					// },1800)
					loadRefresh()
				} else {
					isRefreshing = false
				}
			}
		})
	}
	function loadRefresh(){//  retoken
		let dataes = {
			"access_token": "",
			"vtype": "login",
			"companyid": uni.getStorageSync("companyid"),
			"userid": uni.getStorageSync("userid"),
			"password": uni.getStorageSync("password"),
			"fdbh": uni.getStorageSync("fdbh"),
			"computerid": uni.getStorageSync("uuid"),//
			"ipaddress": uni.getStorageSync("ip")
		}
		uni.request({
			url: "http://self.mzsale.com/mzato/main/app/usercheck",
			data: dataes,
			method: "POST",
			header: {
				"Content-Type": "application/json"
			},
			success: (res) => {
				console.log("APP  refresh_token res", res)
				if (res.data.error_code == 0) {
					uni.setStorageSync("access_token", res.data.access_token);
					uni.setStorageSync("refresh_token", res.data.refresh_token);
					uni.setStorageSync("expires_in", res.data.expires_in);
					isRefreshing = true
					let token = res.data.access_token
					// token
					requests.forEach(item => item(token))
					requests = []
				} else {
					isRefreshing = false
					uni.$u.toast(res.data.message)
				}
			}
		})
	}

}
